import React from 'react';




class Img extends React.Component{

    
    render(){
        
        const { img, alt, mouseOver, mouseOut, id}= this.props
        return(
        
            <div>   
                    <img id={id} src={img} onMouseOver={mouseOver} onMouseOut={mouseOut} alt={alt}></img>
                   
            </div>
            
            
            
            
            
                     
        );

        
    }
        
  



}


export default Img

