import React from 'react';
import ReactPlayer from 'react-player';

//IMPORTACION COMPONENTES
import Boton from './Boton'
import '../assets/css/NavStyle.css';
import music from '../assets/sound/musicaJaponesa.mp3'

//IMPORTACION ICONOS
import {faVolumeUp} from '@fortawesome/free-solid-svg-icons';
import {faVolumeMute} from '@fortawesome/free-solid-svg-icons';
import {faBars} from '@fortawesome/free-solid-svg-icons';



//EVENTO NAV SCROLL  
function navVisible(){

   var menu = document.getElementById('contenedorMenu')
   var resolucion = window.innerWidth;

   if(resolucion <= 930){
      menu.style.left='0%'
      menu.style.width='100%'
   }
   

}
function navNoVisible(){
   var menu = document.getElementById('contenedorMenu')
   var resolucion = window.innerWidth;

   if(resolucion <= 930){
      menu.style.left='-200%' 
   }
   
  
}


//CLASE DE NAV
class Nav extends React.Component{

   constructor (props){
      super(props);
      this.state = {
         playing: false,
         icon: faVolumeMute,
         verNav: false,
      }
      this.playSong = this.playSong.bind(this);
      this.pauseSong = this.pauseSong.bind(this);
      this.mostrarNav = this.mostrarNav.bind(this);
      this.ocularNav = this.ocularNav.bind(this);
   }
    
   //EVENTOS BOTON SONIDO
   playSong(){

      this.setState({playing: true});
      this.setState({icon: faVolumeUp});
   }

   pauseSong(){
      this.setState({playing: false});
      this.setState({icon: faVolumeMute});
   }
 
   mostrarNav(){

      this.setState({verNav: true});
      navVisible();
   }

   ocularNav(){
      this.setState({verNav: false});
      navNoVisible();
   }
   
   //EVENTO BOTON NAV
 
   

  render()
  {     
         
            
     return(
        
      <section id='barra'>
            
         
           <div>
           <Boton          
             id='btnMostrarNav'
             icon={faBars}
             onClick={this.state.verNav ? this.ocularNav: this.mostrarNav }
            
             />
           </div>
           
               <ReactPlayer 
                  ref='player' 
                  url= {music}
                  playing={this.state.playing}
                  width='0%'
                  loop = {true}
                  height='0%'
               />
               <div id="contenedorMenu">

                  <div>
                  <li><a className='link' href="index.html" >Inicio</a></li>
                  </div>
                  <div>
                  <li><a className='link' href="https://www.w3schools.com"  >Ventana1</a></li>
                  </div>
                  <div>
                  <li><a className='link' href="https://www.w3schools.com"  >Ventana2</a></li>
                  </div>
                  <div>
                  <li><a className='link' href="https://www.w3schools.com"  >Ventana3</a></li>
                  </div>
                  
                  

               </div>  
                  
               <Boton   
                        id='btnOn' 
                        icon={this.state.icon} 
                        onClick={this.state.playing ? this.pauseSong : this.playSong}
               />
               <h1 id='titleNav'>ミスックアル</h1>

      </section>
        
        
     );
    
  }
  
}
        
export default Nav;
