//JAVASCRIPT
//EVENTO IMAGEN SALIENDO DE LA DERECHA

window.addEventListener("scroll", (e)=>{
    var scrolled = window.scrollY;
    var gutsAnimado = document.getElementById('imagenAparicion')
    
        gutsAnimado.style.width='0%'
        gutsAnimado.style.marginLeft='0%'   //Movimiento de la imagen en el eje X derecha
    
    if(scrolled > 40){
        gutsAnimado.style.width='80%'
        
    }else{
        
        gutsAnimado.style.width='0%'
        gutsAnimado.style.marginLeft='30%'
    }
    
    if(scrolled > 1565){
        gutsAnimado.style.width='0%'
        gutsAnimado.style.marginLeft='30%'
    }
});
//EVENTO IMAGEN LOCAL AMPLIANDO SU TAMAÑO Y SALIENDO ITACHI
function eventoMouseOver(){
    
    var resolucion = window.innerWidth;
    var PersonajeAnime = document.getElementById('personajeAnime')
    var imagenAnimada = document.getElementById('imagenSeccion3Local')

    if(resolucion <= 500){
       
        PersonajeAnime.style.opacity='1'
        PersonajeAnime.style.width='60vw'
        PersonajeAnime.style.marginLeft='30vw'
        
        imagenAnimada.style.filter= 'brightness(50%)'
        imagenAnimada.style.width='80vw'
        imagenAnimada.style.height='140vw'
        imagenAnimada.style.margin='5% 0% 0% 8%'  

    }
    else{
        
        if(resolucion > 500 && resolucion <= 1100){
            PersonajeAnime.style.opacity='1'
            PersonajeAnime.style.width='40vw'
            PersonajeAnime.style.height='60vw'
            PersonajeAnime.style.marginTop='-100%'
            PersonajeAnime.style.marginLeft='44vw'
            
            imagenAnimada.style.filter= 'brightness(50%)'
            imagenAnimada.style.width='70%'
            imagenAnimada.style.height='95vw'
            imagenAnimada.style.margin='5% 0% 0% 15%' 

        }
        if(resolucion > 1100 && resolucion < 3000){

            PersonajeAnime.style.opacity='1'
            PersonajeAnime.style.width='20vw'
            PersonajeAnime.style.height='40vw'
            PersonajeAnime.style.marginLeft='13vw'
            PersonajeAnime.style.marginTop='-100%'
            

            imagenAnimada.style.filter= 'brightness(50%)'
            imagenAnimada.style.width='80%'
            imagenAnimada.style.height='90vh'
            imagenAnimada.style.margin='4% 5% 0% 15%' 
            
        }
         
    }
    
}

//EVENTO ACHICAR IMAGEN LOCAL + ITACHI ESCONDIENDOSE    
function eventoMouseOut()
{
    var resolucion = window.innerWidth;
    var PersonajeAnime = document.getElementById('personajeAnime')
    var imagenAnimada = document.getElementById('imagenSeccion3Local')
   

    if(resolucion <= 500){
        PersonajeAnime.style.opacity='0'
        PersonajeAnime.style.width='0vw'
        PersonajeAnime.style.marginLeft='0vw'
        
        imagenAnimada.style.filter= 'brightness(100%)'
        imagenAnimada.style.width='70vw'
        imagenAnimada.style.height='120vw'
        imagenAnimada.style.margin='4% 0% 0% 15%'  
    }
    else{
        if(resolucion > 500 && resolucion <= 1100){
        PersonajeAnime.style.opacity='0'
        PersonajeAnime.style.width='0vw'
        PersonajeAnime.style.marginLeft='0vw'
        
        imagenAnimada.style.filter= 'brightness(100%)'
        imagenAnimada.style.width='60%'
        imagenAnimada.style.height='80vw'
        imagenAnimada.style.margin='5% 0% 0% 22%'  
        }

        if(resolucion > 1100 && resolucion < 3000){

            PersonajeAnime.style.opacity='0'
            PersonajeAnime.style.width='0vw'
            PersonajeAnime.style.marginLeft='0vw'
            PersonajeAnime.style.marginTop='-80%'
            

            imagenAnimada.style.filter= 'brightness(100%)'
            imagenAnimada.style.width='70%'
            imagenAnimada.style.height='80vh'
            imagenAnimada.style.margin='4% 5% 0% 15%' 
            
        }
       
    }
       
}
