import React from 'react'




class SectionTexto1Foto extends React.Component{

    
  render(){
      const { title, parrafo, idSectionTexto, idSectionImg, img, imgAlt, animacion} = this.props

      
     
        return (

            <section>

                    <section id={idSectionTexto}>

                        <h1>{title}</h1>
                        <p>{parrafo}</p>

                    </section>

                    <section id={idSectionImg}>
                    
                        <img id={animacion} src={img} alt={imgAlt}/>
                    

                    </section>

            </section>
               

        );

    }
        
                
}

export default SectionTexto1Foto;